body {
  overflow: hidden;
  position: fixed;
  width:100%;
  height:100%;
  display: block;
}
.app {
  background-image: url('https://free4kwallpapers.com/uploads/originals/2020/05/25/firewatch-video-game-.-thank-you-for-those-who-corrected-me_.-wallpaper_.jpg');
  background-size: cover;
  width: 100%;
  height: 100vh;
  display: block;
  overflow: hidden;
  position: fixed;
  top:0;
}

.page {
  width: 100%;
  height: 100vh;
  display: block;
  background: rgba(0,0,0,.2);
  position: fixed;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  top: 100px;
}

.stock-list {
  width: 100%;
  padding:20px;
  box-sizing: border-box;
  margin-bottom: 150px;
}

.stock {
  color: #fff;
  width: calc(33% - 17px);
  height: 200px;
  background: rgba(36,36,36,.7);
  display: inline-block;
  margin:10px;
  box-sizing: border-box;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);

  text-shadow: 0px 0px 5px rgba(0, 0, 0, 1);
}

.indicator {
  width:15px;
  height: 15px;
  display: block;
  position: fixed;
  z-index: 1;
  border-radius: 50%;
  top:10px;
  right:10px;

  -webkit-box-shadow: 0px 0px 129px 6px rgba(0,0,0,1);
-moz-box-shadow: 0px 0px 129px 6px rgba(0,0,0,1);
box-shadow: 0px 0px 129px 6px rgba(0,0,0,1);
}

.connection_offline {
  animation: offline 1200ms infinite!important;
}

.connection_online {
  animation: online 1200ms infinite!important;
}

@keyframes offline {
  0% {
    background: red;
  }
  50% {
    background: #c7402e;
  }
  100% {
    background:red;
  }
}

@keyframes online {
  0% {
    background: green;
  }
  50% {
    background: #48a360;
  }
  100% {
    background:green;
  }
}

@media only screen and (max-width: 800px) {
  .stock {
    width: calc(100% - 20px);  
  }

  .page {
    overflow: scroll!important;
    -webkit-overflow-scrolling: auto; /* Lets it scroll lazy */
  }
}
.direction {
  font-weight: normal;
  font-size: 13px;
}

.animateSpin {
  animation: spin 500ms infinite;
}

@keyframes spin {
  from {
      transform:rotate(0deg);
  }
  to {
      transform:rotate(360deg);
  }
}

.tiker {
  font-weight: normal;
  font-size: 11px;
  margin-right:5px;
}
.decrease {
 color: rgb(161, 11, 44);
 animation: movedown 1300ms infinite;
}


.increase {
  color: green;
  animation: movedown 1300ms infinite; 
}

@keyframes movedown {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.stock .top-bar {
  width: 100%;
  height: 60px;
  background:rgba(13, 36, 74, .2);
}

.stock .bottom-bar {
  width: 100%;
  height: 140px;
  background:rgba(13, 36, 74, .4);
}

.top-bar .title-content {
  float: left;
  margin: 10px;
  text-align: left;
}

.top-bar .title {
  display: block;
  font-weight: bold;
  font-size: 16px;
}

.top-bar .symbol {
  display: inline-block;
  font-size: 12px;
  margin-right: 10px;
}

.top-bar .marketPrice {
  float: right;
  margin: 10px;
  text-align: right;
}

.marketPrice.profit .change, .marketPrice.profit .changePercent, .pr-lo-change.profit, .changeContent.profit, .tiker.profit {
  color: forestgreen;
}

.marketPrice.lost .change, .marketPrice.lost .changePercent, .pr-lo-change.lost, .changeContent.lost, .tiker.lost {
  color: rgb(161, 11, 44);
}

.marketPrice .price {
  display: block;
  font-weight: bold;
  font-size: 16px;
}
.counter {
  width: 100%;
  color:#fff;
  text-align: center;
  font-weight: bold;
  font-size: 45px;
  display: none;
}
.marketPrice .change {
  display: inline-block;
  font-size: 14px;
  margin-right: 5px;
}

.marketPrice .changePercent {
  display: inline-block;
  font-size: 14px;
}

.bottom-bar .hold {
  margin: 10px;
  float:left;
  font-size: 14px;
  opacity: .5;
  display: block;
  width: 100%;
}

.bottom-bar .worth {
  margin: 10px;
  margin-right: 0;
  float:left;
  font-size: 22px;
  font-weight: bold;
}

.bottom-bar .worth-USD {
  margin: 10px;
  float:left;
  font-size: 14px;
}

.bottom-bar .pr-lo-change {
  float: left;
  width: 100%;
}
.bottom-bar .pr-lo {
  margin: 10px;
  float:left;
  font-size: 22px;
  margin-right: 0;
}

.bottom-bar .pr-lo-percent {
 
  margin: 10px;
  margin-left: 5px;
  float:right;
  font-size: 29px;
  margin-top: 12px;
  opacity: .1;
  color:#fff;
}

.usdprice {
  opacity: .3;
  margin-left: 5px;
  display: inline-block;
  
}
.pr-lo .small {
  font-size: 12px;
  position: relative;
  top:-10px;
}
.small.sek {
  font-weight: light;
}

.reload-btn {
  border-radius: 8px;
  width: 110px;
  height:40px;
  position: fixed;
  display: block;
  bottom:10px;
  left:calc(50% - 55px);
  color: #fff;
  background: #33568f;
  text-align: center;
  line-height: 40px;
  z-index: 2;
}

.wallet-content {
  width:100%;
  height: 80px;
  padding-top:20px;
  backdrop-filter: blur(14px);
  -webkit-backdrop-filter: blur(14px);
  color:#fff;
  text-align: center;
  position: fixed!important;
  z-index: 1;
  background: rgba(0,0,0,.3);

  text-shadow: 0px 0px 5px rgba(0, 0, 0, 1);
}

.wallet-content .balance {
  font-size: 28px;
  font-weight: bold;
}

.wallet-content .balance .small.sek {
  font-weight: normal!important;
  font-size: 15px;
}

.wallet-content .balance, .wallet-content .changeContent {
  width: 100%;
  height:35px;
  line-height: 35px;
}

.wallet-content .change, .wallet-content .changePercent {
  display: inline-block;
}

.wallet-content .change {
  font-size: 19px;
  font-weight: bold;
  margin-right: 10px;
}

.wallet-content .change .small.sek {
  font-size: 14px;
  font-weight: normal;
  top:0px;
}

.wallet-content .change .small {
  font-size: 12px;
  position: relative;
  top:-10px;
}
